/* Blanco.css */
body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

.container {
  display: grid;
  grid-template-columns: 55% 45%;
  height: 93vh;
}

.left-column,
.right-column {
  display: grid;
}

.left-column {
  grid-template-rows: 27vh 66vh;
}

.right-column {
  grid-template-rows: 40vh 60vh;
  position: relative;
}

.right-top {
  background-color: #fdfdfd;
  z-index: 2;
  margin-left: 20%;
  display: grid;
  align-items: center;
  grid-template-columns: 70% 30%;
  gap: 10px;
}

.right-bottom {
  background-color: #040405;
  z-index: 1;
  position: absolute;
  height: 93vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 5%;
  width: 100%;
  box-sizing: border-box;
}

.left-top,
.left-bottom {
  border: 1px solid #000;
  border-top: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.left-top {
  flex-direction: row;
  justify-content: space-around;
}

.left-bottom {
  background-color: #fdfdfd;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 5%;
  width: 100%;
  box-sizing: border-box;
}

.left-bottom .content {
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  max-width: 600px;
  box-sizing: border-box;
}

.left-top-img,
.right-top-img {
  border-radius: 1px;
  object-fit: cover;
}

.right-top-img {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}

.small-square {
  width: 225px;
  height: 200px;
}

.small-square2 {
  width: 310px;
  height: 160px;
}

.rectangle {
  width: 360px;
  height: 200px;
}

.vertical-rectangle {
  width: 150px;
  height: 350px;
}

.gallery-button {
  background-color: #000;
  color: #fff;
  border: none;
  padding: 20px 30px;  ;
  cursor: pointer;
  margin-top: 10px;
}

.right-top-left {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 25px;
  margin: 5px;
}

h3,
h2 {
  margin: 0;
  font-family: "Nunito Sans", sans-serif;
}

.left-bottom h3 {
  color: #000;
  font-size: 2.3rem;
  margin-bottom: 20px;
  font-weight: lighter;
  text-align: left;
}

.right-bottom h2 {
  color: white;
  font-size: 2.2rem;
  margin-top: 40vh;
  font-weight: lighter;
  text-align: left;
  width: 45%;
  padding-right: 25px;
}

.right-bottom h3 {
  color: white;
  font-size: 1.2rem;
  margin-top: 5vh;
  font-weight: lighter;
  text-align: left;
  width: 60%;
  padding-right: 25px;
}

/* Media queries para dispositivos móviles */
@media (max-width: 1500px) {
  .vertical-rectangle {
    width: 125px;
    height: 300px;
  }
  .small-square2 {
    width: 250px;
    height: 140px;
  }
  .rectangle {
    width: 300px;
    height: 140px;
  }
  .small-square {
    width: 220px;
    height: 140px;
  }
  .right-top-left {
    margin: 0;
    gap: 15px;
  }
  .right-bottom h3 {
    font-size: 1.3rem;
  }
}

@media (max-width: 1100px) {
  .small-square2 {
    width: 220px;
    height: 140px;
  }
  .vertical-rectangle {
    width: 100px;
    height: 290px;
  }
  .left-bottom {
    width: 100%;
  }
  .left-bottom h3 {
    font-size: 1.8rem;
  }
  .rectangle {
    display: none;
  }
  .small-square {
    width: 200px;
    height: auto;
  }
}

@media (max-width: 768px) {
  .right-top {
    grid-template-columns: 100% 0%;
  }
  .vertical-rectangle {
    display: none;
  }
  .left-top {
    justify-content: center;
    gap: 15px;
  }
  .small-square {
    width: 155px;
    height: auto;
  }
  .left-bottom h3 {
    font-size: 1.2rem;
  }
  .left-bottom {
    padding: 15%;
    width: 100%;
  }
  .gallery-button {
    padding: 10px 20px;
  }
  .right-bottom h2 {
    width: initial;
  }
  .right-bottom h3 {
    width: initial;
  }
}

@media (max-width: 500px) {
  .small-square2 {
    width: 145px;
    height: auto;
  }
  .small-square:nth-of-type(1) {
    display: none;
  }
  .small-square {
    width: 180px;
    height: auto;
  }
}

@media (width: 430px) and (height: 932px) {
  .right-bottom h2 {
    color: white;
    font-size: 2rem;
    padding-right: 15px;
  }
}

@media (max-width: 425px) {
  .small-square {
    width: 150px;
    height: auto;
  }
  .small-square2 {
    width: 150px;
    height: auto;
  }
  .left-bottom h3 {
    font-size: 1.2rem;
  }
  .left-bottom {
    padding: 15%;
    width: inherit;
    border-left: none;
  }
  .right-bottom h2 {
    font-size: 1.2rem;
  }
  .right-bottom h3 {
    width: initial;
  }
  .right-top {
    margin-left: 0%;
  }
  .container {
    height: 100vh;
  }
  .left-column {
    grid-template-rows: 30vh 70vh;
  }
  .right-bottom {
    height: 100vh;
  }
}

@media (max-width: 375px) {
  .small-square2 {
    height: 100px;
    width: auto;
  }
}
