/* src/components/Header.css */
header {
  font-family: "Nunito Sans", sans-serif;
  font-size: 1.7rem;
  position: fixed; 
  top: 0; 
  left: 0; 
  width: 100%; 
  height: 5vh;
  background-color: #020202;
  display: flex;
  justify-content: space-between; 
  align-items: center;
  z-index: 1000; 
  padding: 10px 20px; /* Espaciado interno para el header */
}

nav {
  display: flex; /* Usa flexbox para el diseño */
  margin-right: 35px;
}

.nav-links {
  display: flex;
  justify-content: flex-end; 
  align-items: center;
  list-style: none; 
  margin: 0; /* Elimina el margen */
  padding: 0; /* Elimina el relleno */
}

.nav-links li {
  margin: 0 15px; /* Margen entre los elementos del menú */
}

.nav-links a {
  text-decoration: none; /* Sin subrayado */
  font-family: "Nunito Sans", sans-serif;
  font-weight: lighter;

  color: white; /* Color del texto */
}

.logo {
  display: flex; /* Usa flexbox para el diseño */
  align-items: center; /* Centra verticalmente el logo */
}

.logo-icon {
  height: 60px; /* Tamaño del logo */
}

/* Estilos específicos para pantallas de portátiles */
@media (max-width: 1440px) {
  header {
    height: 6vh; /* Aumenta el alto del header para pantallas más pequeñas */
    padding: 15px 25px; /* Ajusta el espaciado interno */
    font-size: 1.3rem;
  }

  .nav-links {
    margin-right: 20px; /* Ajusta el margen derecho del nav para pantallas más pequeñas */
  }

  .nav-links li {
    margin: 0 10px; /* Ajusta el margen entre los elementos del menú */
  }

  .logo-icon {
    height: 50px; /* Ajusta el tamaño del logo para pantallas más pequeñas */
  }
}

/* Estilos para pantallas más pequeñas (como tabletas y móviles) */
@media (max-width: 1024px) {
  header {
    height: 4vh; /* Ajusta la altura del header para pantallas más pequeñas */
    padding: 10px 15px; /* Ajusta el espaciado interno */
  }

  .nav-links {
    margin-right: 10px; /* Ajusta el margen derecho del nav */
  }

  .nav-links li {
    margin: 0 8px; /* Ajusta el margen entre los elementos del menú */
  }

  .logo-icon {
    height: 40px; /* Ajusta el tamaño del logo */
  }
}
@media (max-width: 1024px) {
  header {
    width: 100%;
  }
}
@media (max-width: 770px) {
  header {
    font-size: 1rem;
  }
}

@media (max-width: 450px) {



.App-header {
  position: inherit; 

  height: 100vh;
  display: flex;
  display: none;
  align-items: center; 
  justify-content: center; 
  flex-direction: column;
  opacity: 1; /* Inicialmente completamente visible */
  overflow: hidden; /* Oculta el contenido que se desborda durante la animación */
  /* animation: fadeOutHeight 8s forwards;  */
  padding: 0px;
}
.logo{
  flex-direction: column;

}
.logo-icon {
  height: 200px;
 width: 100%;
 max-width: inherit;
}
nav{
  display: none;
}
.logo{

}

@keyframes fadeOutHeight {
  0% {
    height: 100vh; /* Comienza con altura completa */
    opacity: 1; /* Comienza completamente visible */
  }

  100% {
    height: 0vh; /* Reduce la altura a 0 */
    /* opacity: 0;  */
  }
}

.hidden {
  display: none; /* Oculta el elemento completamente después de la animación */
}

.logo {
  text-align: center;
}


}