/* Hombre.css */

.hombre-container {
  width: 100%;
  height: 100vh; /* Ocupa toda la altura de la ventana */
  display: flex;
  flex-direction: column;
  box-sizing: border-box; /* Incluye padding y borde en el cálculo del tamaño total */
}

.hombre-image {
  width: 100%;
  height: 86vh; /* Deja espacio para el texto debajo de la imagen */
}

.hombre-image img {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Asegura que la imagen cubra completamente el contenedor */
  display: block;
}

.hombre-text {
  height: 8vh; /* Altura para el texto */
  background-color: #000; /* Fondo negro */
  display: flex;
  align-items: center; /* Centra el texto verticalmente */
  justify-content: end; /* Alinea el texto a la derecha */
  padding-right: 5%;
}

.hombre-text h2 {
  font-family: "Nunito Sans", sans-serif;
  font-weight: lighter;
  color: #fff; /* Texto blanco */
  margin: 0; /* Elimina márgenes predeterminados */
  font-size: 3rem; /* Tamaño de fuente ajustable */
}
@media (max-width: 450px) {
    .hombre-image {
      height: 90vh;
    }
    .hombre-text{
    font-size: 2rem;
    height: 10vh;
  }
}
@media (max-width: 425px) {
  .hombre-image img {
    object-position: center;
  }
  .hombre-text h2 {
    font-size: 2rem;
  }
}
